.modal__container
    .overlay
        opacity: 0
        visibility: hidden
        pointer-events: none

        position: fixed
        top: 0
        left: 0
        width: 100%
        height: 100%
        background: rgb(50, 56, 77, 0.3)
        backdrop-filter: blur(10px)
        z-index: 20
        transition: 0.3s all

        &._active
            opacity: 1
            visibility: visible
            pointer-events: all

    .modal
        display: none
        flex-direction: column
        align-items: center

        height: fit-content

        position: fixed
        top: 0
        right: 0
        bottom: 0
        left: 0

        margin: auto

        z-index: 30

        background: #fff
        box-shadow: 0px 30px 20px rgba(45, 60, 88, 0.15)
        border-radius: 6px

        &._active
            display: flex

    .m1
        width: 800px

        @include adaptive-value('padding-right',40,0,1)
        @include adaptive-value('padding-left',40,0,1)
        @include adaptive-value('padding-bottom',60,40,1)

        .m1__close
            width: 12px
            height: 12px

            background: url('../img/icons/cross_m.png') 0% 0%/ contain no-repeat

            position: absolute
            top: 17px
            right: 15px

        .m1__basket
            position: relative
            top: -31px

            filter: drop-shadow(0px 30px 20px rgba(45, 60, 88, 0.15))

        .m1__title
            @include adaptive-value('font-size',32,24,1)
            line-height: 36px
            text-align: center

        .m1__main
            @include adaptive-value('margin-top',40,10,1)

            display: flex
            flex-direction: row
            justify-content: space-between

            width: 100%

            .m1__order
                padding-left: 20px

                width: 100%

                .m1__order_title
                    @include adaptive-value('font-size',20,18,1)

                    margin-bottom: 20px

                .m1__order_desc
                    display: flex
                    flex-direction: row
                    justify-content: space-between

                .m1__order_sale
                    display: none

                    margin-top: 20px

                    flex-direction: row
                    justify-content: space-between

                    &._show
                        display: flex

                .horizontal
                    margin: 20px 0

                .m1__total
                    display: flex
                    flex-direction: row
                    justify-content: space-between

                    .b
                        font-size: 18px

                    .m1__total_coast
                        font-size: 18px

                .m1__saved
                    display: none

                    font-size: 14px
                    text-align: right

                    &._show
                        display: block

            .vertical
                width: 1px
                height: 230px

                margin-right: 40px
                margin-left: 40px

            .m1__pay
                width: 310px

                .m1__icons

                .card_container1
                    width: 100%
                    .form-payment__number
                        background: #F0F4FB
                        box-shadow: inset 0px 1px 1px rgba(50, 56, 77, 0.25)
                        border-radius: 6px

                        height: 50px

                        padding: 17px 20px

                        margin-top: 10px

                .form-payment__wrap
                    display: flex
                    flex-direction: row
                    justify-content: space-between

                    width: 100%

                    margin-bottom: 10px

                    .card_container
                        width: 150px
                        .form-payment__date,.form-payment__cvv
                            background: #F0F4FB
                            box-shadow: inset 0px 1px 1px rgba(50, 56, 77, 0.25)
                            border-radius: 6px

                            height: 50px

                            padding: 17px 20px

                        .form-payment__cvv

                #payment-request-button-apple,#payment-request-button
                    width: 100%

        .m1__button_next
            @extend %button

            width: 310px

            margin-top: 10px

    .m2
        @include adaptive-value('padding-right',40,0,1)
        @include adaptive-value('padding-left',40,0,1)
        @include adaptive-value('padding-bottom',60,50,1)

        @include adaptive-value('width',500,310,1)

        .m2__lamp
            position: relative
            top: -30px

        .m2__title
            font-size: 24px
            line-height: 36px
            text-align: center

        .m2__desc
            text-align: center

            margin-top: 10px

        .m2__main
            @include adaptive-value('margin-top',30,10,1)

            display: flex
            flex-direction: column
            align-items: center

            .m2__graph_wrap
                position: relative
                width: fit-content

                .m2__graph
                    @include adaptive-value('width',375,310,1)

                .m2__weight
                    position: absolute
                    @include adaptive-value('top',10,6,1)
                    left: 3px

                    border-radius: 3px
                    background: #fff

                    padding: 3px 10px
                    @include adaptive-value('padding-top',3,0,1)
                    @include adaptive-value('padding-bottom',3,0,1)

                    font-size: 14px

                    &::after
                        content: ''
                        display: block

                        position: absolute
                        bottom: -6px
                        @include adaptive-value('left',26,20,1)

                        border-top: 6px solid #fff
                        border-right: 6px solid transparent
                        border-left: 6px solid transparent

                .m2__without
                    position: absolute
                    @include adaptive-value('top',33,30,1)
                    @include adaptive-value('right',39,5,1)

                    border-radius: 3px
                    background: #fff

                    padding: 3px 10px
                    @include adaptive-value('padding-top',3,0,1)
                    @include adaptive-value('padding-bottom',3,0,1)

                    font-size: 14px

                    &::after
                        content: ''
                        display: block

                        position: absolute
                        bottom: -6px
                        @include adaptive-value('left',39,24,1)

                        border-top: 6px solid #fff
                        border-right: 6px solid transparent
                        border-left: 6px solid transparent

                .m2__with
                    position: absolute
                    @include adaptive-value('top',92,75,1)
                    @include adaptive-value('right',57,33,1)

                    border-radius: 3px
                    background: #fff

                    padding: 3px 10px
                    @include adaptive-value('padding-top',3,0,1)
                    @include adaptive-value('padding-bottom',3,0,1)

                    font-size: 14px

                    &::after
                        content: ''
                        display: block

                        position: absolute
                        bottom: -6px
                        @include adaptive-value('left',24,20,1)

                        border-top: 6px solid #fff
                        border-right: 6px solid transparent
                        border-left: 6px solid transparent

                .m2__today
                    position: absolute
                    bottom: -4px
                    @include adaptive-value('left',19,10,1)

                    font-size: 14px

                    font-size: 12px

                .m2__mounth
                    position: absolute
                    @include adaptive-value('right',85,65,1)
                    bottom: -4px

                    font-size: 12px

            .m2__graph_desc
                text-align: center
                font-size: 12px
                line-height: 18px

                margin-top: 10px

                @include adaptive-value('margin-right',0,48,1)
                @include adaptive-value('margin-left',0,48,1)

            .m2__care
                background: #F0F4FB
                border-radius: 6px

                margin-top: 30px

                padding: 20px

                text-align: center

        .js-modal-close
            @extend %button

            width: 300px

            @include adaptive-value('margin-top',30,10,1)

            $circle: #fff
$loader-size: 50px
$circle-size: 12px
$calc-center: $loader-size / 2 - $circle-size / 2

.m1__load_title
    font-size: 18px
    text-align: center
    font-weight: 600

    margin-top: 10px

.m1__load_desc
    text-align: center
    margin-top: 10px

.m1__loading
    display: none

.spiner
    position: relative

    width: $loader-size
    height: $loader-size

    margin: 0 auto
    margin-top: 10px

    &__container
        position: absolute
        top: 0
        left: 0
        width: $loader-size
        height: $loader-size
        &_2
            transform: rotate(45deg)
    &__circle
        width: $circle-size
        height: $circle-size
        border-radius: 50%
        background: #8991AE
        transform: scale(0%)
        position: absolute
        &_1
            top: 0
            left: $calc-center
        &_2
            right: 0
            top: $calc-center
        &_3
            bottom: 0
            left: $calc-center
        &_4
            left: 0
            top: $calc-center

@for $i from 1 through 8
    .spiner__circle_animation-#{$i}
        animation: spiner 1s .123s * $i infinite

@keyframes spiner
    0%
        transform: scale(100%)
    100%
        transform: scale(10%)
