
main
    width: 100vw
    min-height: 100vh
    //min-height: 1080px
    position: relative

.d
    color: #32384D !important
.g
    color: #8991AE !important
.w
    color: #CED4E4 !important
.ww
    color: #fff !important
.r
    color: #F34E4E !important
.o
    color: #F4955F !important
.green
    color: #25AB62 !important

.container
    display: flex
    justify-content: center
    align-items: center

    flex-direction: column

    max-width: 1600px
.bg
    position: absolute

    width: 100%
    height: 100%

    pointer-events: none

    background: linear-gradient(107.56deg, #FFFFFF 0%, #F6F9FF 23.44%, #DEE3F0 42.71%, #F6F9FF 67.04%, #DEE3F0 81.77%)

    z-index: -10

    overflow: hidden

.static-bottom
    width: 100vw

    display: none
    flex-direction: row
    justify-content: center
    align-items: center

    position: absolute
    bottom: 0

    margin: 0 auto
    margin-bottom: 26px
    .static-bottom__top
        @include adaptive-value('margin-right',98,0,2)
    .static-bottom__top,.static-bottom__bottom
        font-size: 16px
        line-height: 26px
        text-align: center
        color: #000
    .static-bottom__middle
        font-size: 16px
        line-height: 26px
        text-align: center
        a
            color: #000
            &:not(:last-of-type)
                margin-right: 15px

    &._show
        display: flex

@media (max-width: 500px)
    .static-bottom
        flex-direction: column

        .static-bottom__top
            margin-right: 0
            margin-bottom: 10px
